.parallax {
  perspective: 100px;
  height: 900px;
  overflow-x: hidden;
  overflow-y: auto; }
  @media screen and (max-width: 420px) {
    .parallax {
      height: 100vh; } }

.parallax__group {
  position: relative;
  height: 900px;
  transform-style: preserve-3d; }
  @media screen and (max-width: 420px) {
    .parallax__group {
      height: 100vh; } }

.parallax__layer {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0; }

.parallax__layer--base {
  transform: translateZ(0) scale(1); }

.parallax__layer--norm {
  transform: translateZ(-30px) scale(1.3); }

.parallax__layer--moderate {
  transform: translateZ(-40px) scale(1.4); }

.parallax__layer--slow {
  transform: translateZ(-80px) scale(1.8); }

.parallax__layer--back {
  transform: translateZ(-100px) scale(2); }
