.pink {
  background: #FFAFD1; }

.layer, .hill, .clouds, .leafs-r, .leafs-l, .phone {
  background-position: top center;
  background-repeat: no-repeat; }

.layer {
  position: absolute;
  width: 100%;
  height: 900px;
  top: 0;
  left: 0;
  text-align: center; }

.hill {
  background-color: #FFAFD1;
  background-image: url("/static/img/bg_without_logo.png"); }

.clouds {
  background-image: url("/static/img/clouds.png"); }

.logo {
  font-size: 1em;
  color: white;
  display: flex;
  flex-direction: column;
  padding-right: 40%;
  padding-bottom: 5%;
  justify-content: center;
  align-items: stretch;
  height: 900px; }
  @media screen and (max-width: 610px) {
    .logo {
      padding-right: 15%;
      padding-bottom: 5%; } }

.logo div {
  width: 100%;
  font-size: 2em;
  font-weight: 300;
  text-shadow: 0px 2px 6px rgba(186, 43, 167, 0.24); }
  @media screen and (max-width: 610px) {
    .logo div {
      text-shadow: 0px 2px 3px rgba(186, 43, 167, 0.24);
      font-size: 1.2em; } }

.logo h1 {
  font-size: 4em;
  font-weight: 400;
  padding: 0;
  margin: 0; }
  @media screen and (max-width: 610px) {
    .logo h1 {
      font-size: 3.5em; } }

.leafs-r {
  background-image: url("/static/img/leafs_1-r.png"); }

.leafs-l {
  background-image: url("/static/img/leafs_1-l.png"); }

.phone {
  background-image: url("/static/img/phone.png"); }
