@import url("https://fonts.googleapis.com/css?family=Roboto:300,400,500&subset=cyrillic-ext");
html {
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  min-height: 100vh; }

body {
  font-family: 'Roboto', sans-serif;
  background: #FFFFFF !important;
  min-height: 100%; }

section {
  padding: 40px; }

.bg-light {
  background-color: #FAFAFA; }

.bg-dark {
  background-color: #0055c4;
  color: white; }

.header {
  text-align: center; }

.descr {
  text-align: center;
  font-weight: 300;
  font-size: 2em;
  max-width: 800px;
  margin: 0 auto 40px; }
  @media screen and (max-width: 610px) {
    .descr {
      font-size: 1.8em; } }

h1 {
  font-size: 8em; }

h2 {
  font-size: 5em;
  font-weight: 100;
  margin-bottom: 20px; }
  @media screen and (max-width: 610px) {
    h2 {
      font-size: 3.4em; } }

h3 {
  font-size: 3em;
  font-weight: 100; }

.light {
  font-weight: 100; }

.center {
  text-align: center;
  margin: 0 auto; }

.container {
  width: 100%;
  height: 100%; }

.flex {
  display: flex;
  justify-content: center;
  align-items: center; }

.shadow {
  -webkit-box-shadow: 0px 1px 12px 0px rgba(0, 0, 0, 0.45);
  -moz-box-shadow: 0px 1px 12px 0px rgba(0, 0, 0, 0.45);
  box-shadow: 0px 1px 12px 0px rgba(0, 0, 0, 0.45); }

.shadow-light {
  -webkit-box-shadow: 0 0 4px 0px rgba(0, 0, 0, 0.45);
  -moz-box-shadow: 0 0 4px 0px rgba(0, 0, 0, 0.45);
  box-shadow: 0 0 4px 0px rgba(0, 0, 0, 0.45); }

a,
a.active,
a.visited {
  text-decoration: none; }

.fit {
  object-fit: contain;
  width: 100%;
  height: 100%; }
